@import "./Global.scss";

.pokemon-page {
  background-color: $main-color;
  border-radius: 50px;
  margin: 4em auto;
  height: 675px;
  width: 70%;

  .title {
    display: flex;
    justify-content: center;
    padding-top: 1em;
    margin-bottom: 2em;
    font-family: "Lobster", cursive;
    font-size: 20px;

    .individual-name {
      margin-right: 1em;
    }

    .pokemon-id {
      color: $additional-color;
    }
  }

  .individual-image {
    height: 150px;
    width: 150px;
    margin: 0 4em;
  }

  .base-info {
    display: flex;

    .info-group1 {
      display: flex;
      margin-bottom: 2em;

      .base-stats {
        margin: 0 4em;
      }

      .additional-stats {
        margin: 0 4em;
      }
    }
  }

  .info-group2 {
    text-align: center;
    width: 55%;
    margin: 1em auto 2em auto;
  }

  .info-group3 {
    display: flex;
    justify-content: space-evenly;
    margin-top: 2em;
  }
}

@media (max-width: 1200px) {
  .pokemon-page .individual-image,
  .pokemon-page .base-info .info-group1 .base-stats,
  .pokemon-page .base-info .info-group1 .additional-stats {
    margin: 0 3em;
  }
}

@media (max-width: 1100px) {
  .pokemon-page .individual-image,
  .pokemon-page .base-info .info-group1 .base-stats,
  .pokemon-page .base-info .info-group1 .additional-stats {
    margin: 0 2em;
  }
}

@media (max-width: 920px) {
  .pokemon-page {
    height: 700px;
  }

  .pokemon-page .individual-image,
  .pokemon-page .base-info .info-group1 .base-stats,
  .pokemon-page .base-info .info-group1 .additional-stats {
    margin: 0 1em;
  }
}

@media (max-width: 850px) {
  .pokemon-page {
    height: 950px;

    .info-group1 {
      flex-direction: column;
      text-align: center;
      margin-left: 2em;
    }
  }

  .pokemon-page .base-info .info-group1 .additional-stats {
    margin-top: 2em;
  }
}

@media (max-width: 780px) {
  .pokemon-page {
    height: 1160px;

    .base-info {
      align-items: center;
      flex-direction: column;

      .info-group1 {
        margin-left: 0;
      }
    }

    .info-group2 {
      width: 75%;
    }

    .info-group3 {
      flex-direction: column;
      text-align: center;
    }
  }
}

@media (max-width: 700px) {
  .pokemon-page {
    height: 1200px;
  }
}
